import React, { Component } from "react";
import { Link } from 'react-router-dom';
import moment from 'moment';



class PastEventCards extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            eventName: props.card.eventName,
            eventDetails: props.card.eventDetails,
            date: props.card.date,
            time: props.card.time, 
            valence: props.card.valence, 
            arousal: props.card.arousal 
        };
      }

    render() {
        console.log('state',this.state);
        return (
            <div>
                <div class="card" >
                    <div class="card-body">
                    <h5 class="card-title">{this.props.card.eventName}</h5>
                    <h6 class="card-subtitle mb-2 text-muted">{moment(this.props.card.date).format('dddd, MMMM Do') + ' - ' + this.props.card.time}</h6>
                    <p class="card-text">{this.props.card.eventDetails}</p>
                    
                    </div>
                </div>
                <br />
                <br />
            </div>
        );
    }
}

export default PastEventCards;


